import React, { useState } from 'react';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import { gapi } from 'gapi-script'; // Correct import
import './App.css'; // Import the CSS file

function App() {
  const [textToCopy, setTextToCopy] = useState("هنا انسخ الارقام ");
  const [uploadedImage, setUploadedImage] = useState(null);

  const handleFileChange = (event) => {
    const file = event.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onloadend = () => {
        setUploadedImage(reader.result); // Set the uploaded image data
      };
      reader.readAsDataURL(file);
    }
  };

  const handleDriveUpload = async () => {
    const CLIENT_ID = 'YOUR_CLIENT_ID.apps.googleusercontent.com';
    const API_KEY = 'YOUR_API_KEY';
    const DISCOVERY_DOCS = ['https://www.googleapis.com/discovery/v1/apis/drive/v3/rest'];
    const SCOPES = 'https://www.googleapis.com/auth/drive.file';

    gapi.load('client:auth2', () => {
      gapi.client.init({
        apiKey: API_KEY,
        clientId: CLIENT_ID,
        discoveryDocs: DISCOVERY_DOCS,
        scope: SCOPES,
      }).then(() => {
        const isSignedIn = gapi.auth2.getAuthInstance().isSignedIn.get();
        if (isSignedIn) {
          handleUpload();
        } else {
          gapi.auth2.getAuthInstance().signIn().then(() => {
            handleUpload();
          });
        }
      });
    });
  };

  const handleUpload = () => {
    // Implement your logic to access Google Drive and upload the image
    console.log('User can upload files from Google Drive');
  };

  return (
    <div className="container">
      <h1>التعرف على الارقام العربية</h1>

      {/* File input for uploading images */}
      <input 
        type="file" 
        accept="image/*" 
        onChange={handleFileChange} 
        className="file-input" 
      />

      {/* Button to upload from Google Drive */}
      <button className="button" onClick={handleDriveUpload}>
        Upload from Google Drive
      </button>

      {/* Display uploaded image */}
      {uploadedImage && (
        <div>
          <h2>Uploaded Image</h2>
          <img src={uploadedImage} alt="Uploaded" className="captured-image" />
        </div>
      )}

      {/* Text tab with a copy feature */}
      <div className="copy-container">
        <p>{textToCopy}</p>
        <CopyToClipboard text={textToCopy}>
          <button className="copy-button">Copy Text</button>
        </CopyToClipboard>
      </div>
    </div>
  );
}

export default App;
